import axios from '@/libs/api.request'

export const login = (data) => {
    return axios.request({
        // url: '/api/p/accounts/login/',
        url:  window.__POWERED_BY_QIANKUN__ ? '/api/p/accounts/login/' : '/api/accounts/login/',
        method: 'post',
        data
    })
}

export const Authorization = () => {
  console.info(window.__POWERED_BY_QIANKUN__, process.env.NODE_ENV)
  if (process.env.NODE_ENV !== 'development') {
    return axios.request({
      url: '/api/accounts/authorization/',
      method: 'get'
    })
  } else {
    return axios.request({
      url: window.__POWERED_BY_QIANKUN__ ? '/api/p/accounts/authorization/' : '/api/accounts/authorization/',
      // url:  '/accounts/authorization/',
      method: 'get'
    })
  }
}

export const getUserInfo = (token) => {
    return axios.request({
        url: 'get_info',
        params: {
            token
        },
        method: 'get'
    })
}

export const logout = (token) => {
    return axios.request({
        url:  window.__POWERED_BY_QIANKUN__ ? '/api/p/accounts/logout/' : '/accounts/logout/',
        method: 'get'
    })
}

export const getUnreadCount = () => {
    return axios.request({
        url: 'message/count',
        method: 'get'
    })
}

export const getMessage = () => {
    return axios.request({
        url: 'message/init',
        method: 'get'
    })
}

export const getContentByMsgId = msg_id => {
    return axios.request({
        url: 'message/content',
        method: 'get',
        params: {
            msg_id
        }
    })
}

export const hasRead = msg_id => {
    return axios.request({
        url: 'message/has_read',
        method: 'post',
        data: {
            msg_id
        }
    })
}

export const removeReaded = msg_id => {
    return axios.request({
        url: 'message/remove_readed',
        method: 'post',
        data: {
            msg_id
        }
    })
}

export const restoreTrash = msg_id => {
    return axios.request({
        url: 'message/restore',
        method: 'post',
        data: {
            msg_id
        }
    })
}
